import React from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFacebook, faInstagram, faLinkedinIn } from '@fortawesome/free-brands-svg-icons';
import { Fade } from 'react-reveal';

const Footer = ({extraStyle}) => {
    return (
      <div className="word-section footer justify-center" style={extraStyle}>
        <Fade top cascade>
          <div className="left">
            <h2>NVCTI</h2>
            <p className="lead">
              Lorem ipsum dolor sit amet consectetur adipiscing elit, urna
              consequat felis vehicula class ultricies mollis dictumst, aenean
              non a in donec nulla. Phasellus ante pellentesque erat cum risus
              consequat imperdiet aliquam, integer placerat et turpis mi eros
              nec lobortis taciti, vehicula nisl litora tellus ligula porttitor
              metus.{" "}
            </p>
          </div>
        </Fade>
        <Fade top cascade>
        <div className="right" style={{justifyContent:"normal"}}>
          <h3 className="connect-text">Connect With Us:</h3>
          <div className="social-links">
            {data.map((item, ind) => {
              const { icon, link, name } = item;
              return (
                <span key={item + ind}>
                  <a
                    href={link}
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{ paddingLeft: "0" }}
                  >
                    <FontAwesomeIcon icon={icon} className={name} />
                  </a>
                </span>
              );
            })}
          </div>
        </div>
        </Fade>
        <Fade bottom cascade>
        <div className="bottom-div jusitfy-align-center">
          <p className="first-text my-1">- © IIT (ISM) Dhanbad 2020 -</p>
          <p className="second-text">
            <em>Made with love by SIC </em>
            <span style={{ color: "red" }}>❤</span>
          </p>
        </div>
        </Fade>
      </div>
    );
}

export default Footer;

const data = [
  {
    icon: faFacebook,
    name: "facebook",
    link: "",
  },
  {
    icon: faInstagram,
    name: "instagram",
    link: "",
  },
  {
    icon: faLinkedinIn,
    name: "linkedin",
    link: "",
  },
];
