import React, { Fragment } from "react";
import { Row, Col } from "react-bootstrap";
import Fade from "react-reveal/Fade";
export default function Details() {
  return (
    <Fragment>
      <Row
        style={{ marginTop: "150px", marginBottom: "150px",marginLeft:"10px"}}
        className="show-grid justify-center"
      >
        <Col
          lg={4}
          md={4}
          sm={12}
          style={{paddingBottom: "2%" }}
        >
          <Fade>
            <h1>NVCTI,</h1>
            <h3>an Innovation Cell of IIT ISM</h3>
            <hr
              style={{ width: "80%", paddingBottom: "2px", paddingTop: "20px" }}
            ></hr>

            <p style={{ fontSize: "18px" }}>Fostering Innovation</p>
          </Fade>
        </Col>

        <Col
          lg={4}
          md={4}
          sm={12}
          style={{paddingBottom: "2%" }}
        >
          <Fade>
            <p style={{ fontSize: "20px" }}>
              With the mission to provide students with a platform for promoting
              experimentation, innovation and creative output skills, we, at
              NVCTI are putting endeavours to inculcate convoluted thinking in
              an aesthetic approach in the minds of students and faculty members
              by polarizing thoughts into the process and thereby into a
              product.
            </p>
          </Fade>
        </Col>

        <Col
          lg={4}
          md={4}
          sm={12}
          style={{paddingBottom: "2%" }}
        >
          <Fade>
            <p style={{ fontSize: "20px" }}>
              We also promote an innovation ecosystem to subsidize our students
              in their quest to explore and contribute to the world of
              cutting-edge technologies and entrepreneurship.
            </p>
          </Fade>
        </Col>
      </Row>
    </Fragment>
  );
}
