import React, { Fragment } from "react";
import { Row, Col } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBookReader,
  faCommentDots,
  faEdit,
  faTasks,
} from "@fortawesome/free-solid-svg-icons";
import Fade from "react-reveal/Fade";
export default function Flip() {
  return (
    <Fragment>
      <Row
        style={{ marginTop: "150px", marginBottom: "150px" }}
        className="show-grid justify-center"
      >
        <Col
          lg={3}
          md={6}
          sm={12}
          style={{ paddingBottom: "2%", width: "100%" }}
          className="justify-center"
        >
          <Fade>
            <div className="flip-card">
              <div className="flip-card-inner">
                <div className="flip-card-front">
                  <FontAwesomeIcon icon={faBookReader} size="2x" />
                  <h1>Facts</h1>
                </div>
                <div className="flip-card-back">
                  <h1 style={{ marginTop: "40%" }}>Facts</h1>
                  <p>Few numbers and figures which signifies us</p>
                </div>
              </div>
            </div>
          </Fade>
        </Col>

        <Col
          lg={3}
          md={6}
          sm={12}
          style={{ paddingBottom: "2%", width: "100%" }}
          className="justify-center"
        >
          <Fade>
            <div className="flip-card">
              <div className="flip-card-inner">
                <div className="flip-card-front">
                  <FontAwesomeIcon icon={faCommentDots} size="2x" />
                  <h1>Think</h1>
                </div>
                <div className="flip-card-back">
                  <h1 style={{ marginTop: "40%" }}>Think</h1>
                  <p>Bring out your creative ideas and reach us out</p>
                </div>
              </div>
            </div>
          </Fade>
        </Col>

        <Col
          lg={3}
          md={6}
          sm={12}
          style={{ paddingBottom: "2%", width: "100%" }}
          className="justify-center"
        >
          <Fade>
            <div className="flip-card">
              <div className="flip-card-inner">
                <div className="flip-card-front">
                  <FontAwesomeIcon icon={faEdit} size="2x" />
                  <h1>Design</h1>
                </div>
                <div className="flip-card-back">
                  <h1 style={{ marginTop: "40%" }}>Design</h1>
                  <p>
                    Plan your ideas to transform them into a designed project
                  </p>
                </div>
              </div>
            </div>
          </Fade>
        </Col>

        <Col
          lg={3}
          md={6}
          sm={12}
          style={{ paddingBottom: "2%", width: "100%" }}
          className="justify-center"
        >
          <Fade>
            <div className="flip-card">
              <div className="flip-card-inner">
                <div className="flip-card-front">
                  <FontAwesomeIcon icon={faTasks} size="2x" />
                  <h1>Develop</h1>
                </div>
                <div className="flip-card-back">
                  <h1 style={{ marginTop: "40%" }}>Develop</h1>
                  <p>
                    Carry your project in our technologically advanced labs.
                  </p>
                </div>
              </div>
            </div>
          </Fade>
        </Col>
      </Row>
    </Fragment>
  );
}
