import React, { Fragment, useEffect } from "react";
import NavbarComponent from "../components/common/navbar";
import Logo from "../assets/logo/logo-white.png";
import First from "../components/About/First";
import Details from "../components/About/Details";
import Easein from "../components/About/Easein";
import Discover from "../components/About/Discover";
import Footer from "../components/common/footer";
import ImageSlider from "../components/About/Imageslider";
import Flip from "../components/About/Flip";
import Last from "../components/About/Last";
export default function About() {
  useEffect(() => {
    const func = () => {
      document.title = "About Us | NVCTI";
    };
    func();
  }, []);
  return (
    <Fragment>
      <NavbarComponent variant="transparent" logo={Logo} />
      <First />
      <Details />
      <ImageSlider />
      <Easein />
      <Discover />
      <Flip />
      <Last />
      <Footer />
    </Fragment>
  );
}
